.app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
  height: 100vh;
  background-color: black;
}

.container {
  background-color: rgb(228, 186, 186);
  width: 400px;
  border-radius: 10px;
  padding: 30px;
}

.heading_two {
  padding-top: 3px;
  font-weight: 500;
}

.total_amount_card {
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: aliceblue;
}

.card_text{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.total_amount_para{
  text-align: center;
margin-top: 10px;
font-weight: 600;
}

.input_field{
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
}
.buttons{
  margin-top: 13px;
   display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
}